.ProjectContainer{
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: 1fr;
    grid-row-gap: 1em;
    position: relative;
    max-width: 9.5vw;
    min-width: 200px;
    max-height: 9.5vw;
    min-height: 200px;
}
.ProjectImageContainer{
    grid-row: 1;
    position: relative;
    max-width: 9.5vw;
    min-width: 200px;
    max-height: 9.5vw;
    min-height: 200px;
}

.ProjectImage{
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    max-width: 9.5vw;
    min-width: 200px;
    max-height: 9.5vw;
    min-height: 200px;
    border-radius: 10%;
    z-index: -1;
}


.ProjectTextContainer{
    position: absolute;
    z-index: 2;
    max-width: 9.5vw;
    min-width: 200px;
    max-height: 9.5vw;
    min-height: 200px;
    transition:0.5s;
    display: grid;
    justify-items: center;
    align-items: center;
    background-color: rgba(0,0,0,0.2);
    backdrop-filter: sepia(100%);
    border-radius: 10%;
    opacity: 0;
}

.ProjectDescription{
    font-size: 1.2em;
    font-family: 'courier';
    font-weight: bold;
    color: white;
    background-color:black;
    text-align: center;
    z-index: 3;
}
.ProjectUsed{
    font-size: 1.2em;
    font-family: 'courier';
    color: yellow;
    background-color:black;
    text-align: center;
    z-index: 3;
}

.ProjectTextContainer:hover{
    opacity:1;
    cursor: pointer;
}


.ProjectTitle{
    grid-row: 2/3;
    border-radius: 5%;
    font-size: 1vw;
    font-family: 'courier';
    color: white;
    text-align: center;
    z-index: 3;
}