/* All css for the Home Component */


/*Will cover entire page and stay fixed*/
.HomeContainer{
    display: grid;
    position:relative;
    z-index: 1;
    width:100vw;
    height:100vh;

    background-color: black;
}

/*It will be absolute*/
.SketchContainer{
    position: absolute;
    z-index: -20;
    margin-top:5vh;
    justify-self: center;
    width: 66vw;
    height: 66vh;

    animation-name: fade-in;
    animation-delay: 0.0s;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    transition:0.7s ease-in-out;

    opacity:0;
}
.SketchSide{
    transform:translateX(-30%);
}
/* This will house all text for portfolio */
.InfoContainer{
    display: grid;
    position: absolute;
    grid-template-rows:repeat(8,1fr);
    grid-template-columns: repeat(12,1fr);
    width:100%;
    height:100%;
    z-index: 2;
}

/* NOTE: Romaji Ariki uses span style in Home.js. */
.InfoName{
    align-self: center;
    grid-column: 1/5;
    grid-row: 6/7;
    padding-left: 2vw;
    animation-name: fade-in;
    animation-delay: 0.5s;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;

    opacity: 0;
    background-color: black;
    font-size: 4vw;
    font-family: 'Noto Serif JP', serif;
    color:white;
}
.InfoNameIntro{
    margin: 0.5vh;
    margin-left:2vh;
    display: grid;
    text-align: left;
    font-size: 1.1vw;
    font-family: 'courier';
    position: absolute;
    z-index: -1;
}
.InfoText{
    opacity: 0;
    animation-name: fade-in;
    animation-delay: 0.65s;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

.InfoLinks{
    justify-self:center;
    align-self: center;
    grid-column: 6/8;
    grid-row: 7/9;
    animation-name: fade-in;
    animation-delay: 1.0s;
    animation-duration: 0.81s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;

    opacity: 0;
    z-index: 5;
}
.InfoProjectsLink{
    font-size: 2.4vw;
    font-family: 'courier';
    color:white;
    z-index: 5;
}
.InfoLinks:hover{
    cursor: pointer;
    color:#ffefd4;

}

.InfoProjects_Hidden{
    grid-row: 3/9;
    grid-column: 7/13;
    background-color: black;
    opacity: 0;
    display: none;
}

.InfoProjects{
    grid-row: 2/9;
    grid-column: 7/13;
    background: rgba(0,0,0,1);
    opacity: 1;
    z-index: 1;
    margin-left: 5vw;
}
.ProjectsHeader{
    padding-top: 0.5em;
    padding-left:0.5em;
    font-size: 3vw;
    color: white;
    text-align: left;
    font-family: "courier";
    background-color: black;
}
.ProjectsList{
    display: grid;
    grid-template-rows:auto; 
    grid-template-columns:repeat(3,1fr); 
    grid-row-gap: 4em;
    padding-top: 2em;
    justify-items: center; 
    overflow-y: scroll;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
    
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background:transparent;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #525252;
    border-radius:20px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #b3a786;
  }


@keyframes fade-in{
    to {opacity: 1;}
}



